<template>
  <div class="main-content">
    <!--  -->
    <div v-if="showFlag">
      <el-form :inline="true" :model="searchForm" class="form-content">
                                            <el-form-item label="">
                <el-input v-model="searchForm.dingdanbianhao" 
                    placeholder="" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="">
                <el-input v-model="searchForm.shangpinbianhao" 
                    placeholder="" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="">
                <el-input v-model="searchForm.mingcheng" 
                    placeholder="" clearable></el-input>
              </el-form-item>
                                                                  <el-form-item label="">
                <el-input v-model="searchForm.leibie" 
                    placeholder="" clearable></el-input>
              </el-form-item>
                                                                                                                                                                                                                                                                                <el-form-item>
          <el-button round @click="search()"></el-button>
          <el-button
            v-if="isAuth('shangpinjinhuo','')"
            type="primary"
            @click="addOrUpdateHandler()"
            round
          ></el-button>
          <el-button
            v-if="isAuth('shangpinjinhuo','')"
            :disabled="dataListSelections.length <= 0"
            type="danger"
            @click="deleteHandler()"
            round
          ></el-button>
                    <el-button
            v-if="isAuth('shangpinjinhuo','')"
            type="info"
            @click="chartDialog()"
            round
          ></el-button>
                  </el-form-item>
      </el-form>
      <div class="table-content">
        <el-table
            v-if="isAuth('shangpinjinhuo','')"
            :data="dataList"
            border
            v-loading="dataListLoading"
            @selection-change="selectionChangeHandler"
            style="width: 100%;">
            <el-table-column
                type="selection"
                header-align="center"
                align="center"
                width="50">
            </el-table-column>
                                            <el-table-column
                    prop="dingdanbianhao"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.dingdanbianhao}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="shangpinbianhao"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.shangpinbianhao}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="mingcheng"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.mingcheng}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="leibie"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.leibie}}
                    </template>
                </el-table-column>
                                                              <el-table-column prop="tupian" 
                    header-align="center"
                    align="center" 
                    width="200" 
                    label="">
                    <template slot-scope="scope">
                      <div v-if="scope.row.tupian">
                        <img :src="scope.row.tupian.split(',')[0]" width="100" height="100">
                      </div>
                      <div v-else></div>
                    </template>
                  </el-table-column>
                                                            <el-table-column
                    prop="jinjia"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.jinjia}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="jinhuoshuliang"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.jinhuoshuliang}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="zongjiage"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.zongjiage}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="gongyingshang"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.gongyingshang}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="dingdanriqi"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.dingdanriqi}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="beizhu"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.beizhu}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="yonghuming"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.yonghuming}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="xingming"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.xingming}}
                    </template>
                </el-table-column>
                                                            <el-table-column
                    prop="shouji"
                    header-align="center"
                    align="center"
                    sortable
                    label="">
                    <template slot-scope="scope">
                      {{scope.row.shouji}}
                    </template>
                </el-table-column>
                                                                                                                                                    <el-table-column
                  prop="ispay"
                  header-align="center"
                  align="center"
                  sortable
                  label="">
                  <template slot-scope="scope">
                    <span style="margin-right:10px">{{scope.row.ispay==''?'':''}}</span>
                    <el-button v-if="scope.row.ispay!='' && isAuth('shangpinjinhuo','') " type="text" icon="el-icon-edit" size="small" @click="payHandler(scope.row)"></el-button>
                  </template>
                </el-table-column>
                                                      <el-table-column
                  prop="shhf"
                  header-align="center"
                  align="center"
                  sortable
                  label="">
              </el-table-column>
              <el-table-column
                  v-if="isAuth('shangpinjinhuo','')"
                  prop="sfsh"
                  header-align="center"
                  align="center"
                  sortable
                  label="">
                  <template slot-scope="scope">
                    <span style="margin-right:10px">{{scope.row.sfsh==''?'':''}}</span>
                    <el-button  type="text" icon="el-icon-edit" size="small" @click="shDialog(scope.row)"></el-button>
                  </template>
              </el-table-column>
                        <el-table-column
                header-align="center"
                align="center"
                label="">
                <template slot-scope="scope">
                                <el-button v-if="isAuth('shangpinjinhuo','')" type="text" icon="el-icon-edit" size="small" @click="addOrUpdateHandler(scope.row.id,'info')"></el-button>
                                                <el-button v-if="isAuth('shangpinjinhuo','')" type="text" icon="el-icon-edit" size="small" @click="addOrUpdateHandler(scope.row.id)"></el-button>
                <el-button v-if="isAuth('shangpinjinhuo','')" type="text" icon="el-icon-delete" size="small" @click="deleteHandler(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
                                                                                                                                                                                                                                                                                                <el-pagination
          @size-change="sizeChangeHandle"
          @current-change="currentChangeHandle"
          :current-page="pageIndex"
          :page-sizes="[10, 20, 50, 100]"
          :page-size="pageSize"
          :total="totalPage"
          layout="total, sizes, prev, pager, next, jumper"
          class="pagination-content"
        ></el-pagination>
      </div>
    </div>
    <!-- /  search-->
    <add-or-update v-if="addOrUpdateFlag" :parent="this" ref="addOrUpdate"></add-or-update>

    
        <el-dialog
      title=""
      :visible.sync="sfshVisiable"
      width="50%">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="">
          <el-select v-model="shForm.sfsh" placeholder="">
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" :rows="8" v-model="shForm.shhf"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="shDialog"> </el-button>
        <el-button type="primary" @click="shHandler"> </el-button>
      </span>
    </el-dialog>
    
        <el-dialog
      title=""
      :visible.sync="chartVisiable"
      width="800">  
                                                                                                          <div id="jinhuoshuliangChart" style="width:100%;height:600px;"></div>
                                                                                                                                                                <span slot="footer" class="dialog-footer">
        <el-button @click="chartDialog"></el-button>
      </span>
    </el-dialog>
      </div>
</template>
<script>
import AddOrUpdate from "./add-or-update";
export default {
  data() {
    return {
                                                                                                                                                                                                                                                                                                                                                                              searchForm: {
        key: ""
      },
      dataList: [],
      pageIndex: 1,
      pageSize: 10,
      totalPage: 0,
      dataListLoading: false,
      dataListSelections: [],
      showFlag: true,
      sfshVisiable: false,
      shForm: {},
      chartVisiable: false,
      addOrUpdateFlag:false,
          };
  },
  mounted() {
    this.init();
    this.getDataList();
  },
  filters: {
    htmlfilter: function (val) {
      return val.replace(/<[^>]*>/g).replace(/undefined/g,'');
    }
  },
  components: {
    AddOrUpdate,
      },
  methods: {
            payHandler(row){
      this.$storage.set('paytable','shangpinjinhuo');
      this.$storage.set('payObject',row);
      this.$router.push('pay');
    },
            chartDialog() {
      this.chartVisiable = !this.chartVisiable;
      this.$nextTick(()=>{
                                                                                  // mingcheng mingcheng
                                                                                                                    //  jinhuoshuliang
                                                                                                                                                                                                                                                                                                   // xcolumn ycolumn mingcheng jinhuoshuliang
                var jinhuoshuliangChart = this.$echarts.init(document.getElementById("jinhuoshuliangChart"),'macarons');
        this.$http({
            url: `value/shangpinjinhuo/mingcheng/jinhuoshuliang`,
            method: "get"
        }).then(({ data }) => {
            if (data && data.code === 0) {
                let res = data.data;
                let xAxis = [];
                let yAxis = [];
                let pArray = []
                for(let i=0;i<res.length;i++){
                    xAxis.push(res[i].mingcheng);
                    yAxis.push(res[i].total);
                    pArray.push({
                    value: res[i].total,
                    name: res[i].mingcheng
                    })
                    var option = {};
                                        option = {
                        tooltip: {
                          trigger: 'item',
                          formatter: '{b} : {c}'
                        },
                        title: {
                            text: '',
                            left: 'center'
                        },
                        xAxis: {
                            type: 'category',
                            data: xAxis
                        },
                        yAxis: {
                            type: 'value'
                        },
                        series: [{
                            data: yAxis,
                            type: 'bar'
                        }]
                    };
                                        // 
                    jinhuoshuliangChart.setOption(option);
                    //
                    window.onresize = function() {
                        jinhuoshuliangChart.resize();
                    };
                }
            }
        });
              })
    },
        init () {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  },
    search() {
      this.pageIndex = 1;
      this.getDataList();
    },
    // 
    getDataList() {
      this.dataListLoading = true;
      let params = {
        page: this.pageIndex,
        limit: this.pageSize,
        sort: 'id',
      }
                                          if(this.searchForm.dingdanbianhao!='' && this.searchForm.dingdanbianhao!=undefined){
            params['dingdanbianhao'] = '%' + this.searchForm.dingdanbianhao + '%'
          }
                                                            if(this.searchForm.shangpinbianhao!='' && this.searchForm.shangpinbianhao!=undefined){
            params['shangpinbianhao'] = '%' + this.searchForm.shangpinbianhao + '%'
          }
                                                            if(this.searchForm.mingcheng!='' && this.searchForm.mingcheng!=undefined){
            params['mingcheng'] = '%' + this.searchForm.mingcheng + '%'
          }
                                                            if(this.searchForm.leibie!='' && this.searchForm.leibie!=undefined){
            params['leibie'] = '%' + this.searchForm.leibie + '%'
          }
                                                                                                                                                                                                                                                                                                                            this.$http({
        url: "shangpinjinhuo/page",
        method: "get",
        params: params
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.dataList = data.data.list;
          this.totalPage = data.data.total;
        } else {
          this.dataList = [];
          this.totalPage = 0;
        }
        this.dataListLoading = false;
      });
    },
    // 
    sizeChangeHandle(val) {
      this.pageSize = val;
      this.pageIndex = 1;
      this.getDataList();
    },
    // 
    currentChangeHandle(val) {
      this.pageIndex = val;
      this.getDataList();
    },
    // 
    selectionChangeHandler(val) {
      this.dataListSelections = val;
    },
    // /
    addOrUpdateHandler(id,type) {
      this.showFlag = false;
      this.addOrUpdateFlag = true;
      this.crossAddOrUpdateFlag = false;
      if(type!='info'){
        type = 'else';
      }
      this.$nextTick(() => {
        this.$refs.addOrUpdate.init(id,type);
      });
    },
        // 
    shDialog(row){
      this.sfshVisiable = !this.sfshVisiable;
      if(row){
        this.shForm = {
                    dingdanbianhao: row.dingdanbianhao,
                    shangpinbianhao: row.shangpinbianhao,
                    mingcheng: row.mingcheng,
                    leibie: row.leibie,
                    tupian: row.tupian,
                    jinjia: row.jinjia,
                    jinhuoshuliang: row.jinhuoshuliang,
                    zongjiage: row.zongjiage,
                    gongyingshang: row.gongyingshang,
                    dingdanriqi: row.dingdanriqi,
                    beizhu: row.beizhu,
                    yonghuming: row.yonghuming,
                    xingming: row.xingming,
                    shouji: row.shouji,
                    sfsh: row.sfsh,
                    shhf: row.shhf,
                    ispay: row.ispay,
                    id: row.id
        }
      }
    },
    // 
    shHandler(){
      this.$confirm(`?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "shangpinjinhuo/update",
          method: "post",
          data: this.shForm
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.getDataList();
                this.shDialog()
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    },
        // 
    download(file){
      window.open(`${file}`)
    },
    // 
    deleteHandler(id) {
      var ids = id
        ? [Number(id)]
        : this.dataListSelections.map(item => {
            return Number(item.id);
          });
      this.$confirm(`[${id ? "" : ""}]?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      }).then(() => {
        this.$http({
          url: "shangpinjinhuo/delete",
          method: "post",
          data: ids
        }).then(({ data }) => {
          if (data && data.code === 0) {
            this.$message({
              message: "",
              type: "success",
              duration: 1500,
              onClose: () => {
                this.search();
              }
            });
          } else {
            this.$message.error(data.msg);
          }
        });
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
