<template>
  <div>
    <img class="bg" src="@/assets/img/bg.jpg">
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">
      <h1 class="h1"></h1>
                              <el-form-item v-if="tableName=='xiaoshoubu'" label="" prop="xiaoshoubugonghao">
        <el-input v-model="ruleForm.gonghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xiaoshoubu'" label="" prop="xiaoshoubumima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='xiaoshoubu'" label="" prop="xiaoshoubuyuangongxingming">
        <el-input v-model="ruleForm.yuangongxingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='xiaoshoubu'" label="" prop="xiaoshoubushouji">
        <el-input v-model="ruleForm.shouji" placeholder=""></el-input>
      </el-form-item>
                                                <el-form-item v-if="tableName=='cangku'" label="" prop="cangkuzhanghao">
        <el-input v-model="ruleForm.zhanghao" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='cangku'" label="" prop="cangkumima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='cangku'" label="" prop="cangkuxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='cangku'" label="" prop="cangkushouji">
        <el-input v-model="ruleForm.shouji" placeholder=""></el-input>
      </el-form-item>
                                                <el-form-item v-if="tableName=='caigoubu'" label="" prop="caigoubuyonghuming">
        <el-input v-model="ruleForm.yonghuming" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='caigoubu'" label="" prop="caigoubumima">
        <el-input v-model="ruleForm.mima" placeholder=""></el-input>
      </el-form-item>
                        <el-form-item v-if="tableName=='caigoubu'" label="" prop="caigoubuxingming">
        <el-input v-model="ruleForm.xingming" placeholder=""></el-input>
      </el-form-item>
                                                            <el-form-item v-if="tableName=='caigoubu'" label="" prop="caigoubushouji">
        <el-input v-model="ruleForm.shouji" placeholder=""></el-input>
      </el-form-item>
                                                                                                                                          <el-button @click="login()" class="btn-login" type="primary"></el-button>
    </el-form>
  </div>
</template>
<script>
export default {
  data() {
    return {
      ruleForm: {
              },
      tableName:"",
      rules: {}
    };
  },
  mounted(){
    let table = this.$storage.get("loginTable");
    this.tableName = table;
  },
  methods: {
    // uuid
    getUUID () {
      return new Date().getTime();
    },
    // 
    login() {
                              if((!this.ruleForm.gonghao) && `xiaoshoubu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `xiaoshoubu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.yuangongxingming) && `xiaoshoubu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                              if(`xiaoshoubu` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                  if((!this.ruleForm.zhanghao) && `cangku` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `cangku` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                                                                              if(`cangku` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                  if((!this.ruleForm.yonghuming) && `caigoubu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.mima) && `caigoubu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                            if((!this.ruleForm.xingming) && `caigoubu` == this.tableName){
        this.$message.error(``);
        return
      }
                                                                                                                                                                              if(`caigoubu` == this.tableName && this.ruleForm.shouji&&(!this.$validate.isMobile(this.ruleForm.shouji))){
        this.$message.error(``);
        return
      }
                                                                                                                                                            this.$refs["ruleForm"].validate(valid => {
        if (valid) {
          this.$http({
            url: `${this.tableName}/register`,
            method: "post",
            data:this.ruleForm
          }).then(({ data }) => {
            if (data && data.code === 0) {
              this.$router.replace({ path: "/login" });
            } else {
              this.$message.error(data.msg);
            }
          });
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
